html {
    height: 95%;
}

body {
    margin-left: 0;
    margin-top: 15px;
    font-family: arial;
    font-size: 12px;
    height: 100%;
}

.night-mode {
    background-color: black;
    color: #888;
}

.ess-ee-oh {
    display: none;
}

label {
    display: inline-block;
    margin-bottom: 3px;
}

.outer-container {
    vertical-align: top;
    text-align: left;
    text-align: center;
}

.inner-container {
    display: inline-block;
    text-align: left;
}

.left-column,
#center-column,
.right-column {
    float: left;
}

@media only screen and(max-width:700px) {
    .left-column,
    .right-column {
        display: none;
    }
    #center-column {
        margin-left: 20px;
    }
    body {
        user-select: none;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
    }
    #game {
        tap-highlight-color: rgba(0, 0, 0, 0);
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        -moz-tap-highlight-color: rgba(0, 0, 0, 0);
        width: 500px;
        height: 318px;
        -ms-tap-highlight-color: rgba(0, 0, 0, 0);
    }
}

.left-column {
    margin-right: 10px;
}

.left-column,
.right-column {
    width: 160px;
}

.night-ad {
    display: none;
}

.night-mode .day-ad {
    display: none;
}

.night-mode .night-ad {
    display: block;
}

#game-container {
    position: relative;
    margin-right: 20px;
}

#options-link,
#display-link {
    color: blue;
    text-decoration: underline;
    cursor: pointer;
}

.night-mode #options-link,
.night-mode #display-link,
a {
    color: #069;
}

#game {
    background-color: #C0C0C0;
}

.night-mode #game {
    background-color: #7E7E7E;
}

.dialog {
    background-color: #EEE;
    border: 1px solid #444;
    position: absolute;
    display: none;
    z-index: 1;
}

.night-mode .dialog {
    background-color: #BBB;
    color: #444;
}

.dialog-title {
    color: white;
    font-weight: bold;
    background-color: #06F;
}

.night-mode .dialog-title {
    color: #AAA;
    background-color: #184292;
}

.dialog-bar {
    background-color: #DDD;
}

.night-mode .dialog-bar {
    background-color: #AAA;
}

.dialog-close {
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    line-height: 15px;
    cursor: pointer;
}

.night-mode .dialog-close {
    color: #AAA;
}

.dialog-text-input {
    width: 30px;
    font-size: 12px;
}

.dialog td {
    padding: 3px;
    white-space: nowrap;
}

.dialog input[type="checkbox"],
.dialog input[type="radio"] {
    margin: 0 3px 0 0;
    vertical-align: top;
    padding: 0;
}

#game div {
    float: left;
    line-height: 0;
    font-size: 0;
}

#footer {
    margin-top: 30px;
    margin-bottom: 30px;
    clear: both;
}

#message {
    margin-top: 15px;
    color: green;
    width: 450px;
}

#share {
    display: none;
    width: 480px;
    margin-top: 15px;
    position: relative;
}

#share-close {
    position: absolute;
    top: 0;
    right: 0;
    font-weight: bold;
    cursor: pointer;
    color: #ccc;
}

#share-blurb {
    margin-top: 10px;
    margin-left: 10px;
    padding: 5px;
    background: #f4f4f4;
    position: relative;
}

#share-open-quote {
    position: absolute;
    font-size: 56px;
    line-height: 56px;
    color: #ccc;
    left: -12px;
    top: -11px;
}

#share-close-quote {
    position: absolute;
    font-size: 56px;
    line-height: 56px;
    color: #ccc;
    left: 460px;
    top: 20px;
}

#scores-container {
    margin-top: 20px;
    margin-bottom: 20px;
}

#scores-tabs {
    height: 30px;
}

.scores-tab {
    float: left;
    margin-right: 20px;
    cursor: pointer;
    color: blue;
    text-decoration: underline;
}

.scores-tab-selected {
    font-weight: bold;
    color: black;
}

.night-mode .scores-tab {
    color: #666;
}

.night-mode .scores-tab-selected {
    color: #069;
}

.scores-pane {
    width: 500px;
}

.scores-pane table {
    width: 100%;
}

.scores-pane th,
.scores-pane td {
    width: 33%;
    text-align: left;
    vertical-align: top;
}

.scores-pane th {
    padding-bottom: 5px;
}

.scores-number {
    float: left;
    width: 20px;
    clear: left;
}

.scores-name {
    float: left;
    width: 115px;
}

.flag-animation {
    position: absolute;
    pointer-events: none;
    transition: top .3s, left .3s, width .3s, height .3s, opacity .3s;
    -webkit-transition: top .3s, left .3s, width .3s, height .3s, opacity .3s;
    -moz-transition: top .3s, left .3s, width .3s, height .3s, opacity .3s;
    -ms-transition: top .3s, left .3s, width .3s, height .3s, opacity .3s;
}

